import { Col } from "react-bootstrap";

export const ProjectCard = ({ title, description, imgUrl, navIcon2, navIcon4 , linkLive, linkGit}) => {
 
  if (typeof(navIcon2) !== 'undefined' && navIcon2 != null && typeof(navIcon4) !== 'undefined' && navIcon4 != null) {
    return (
       <Col size={12} sm={6} md={4}>
         <div className="proj-imgbx">
           <img className="proj-img" src={imgUrl} />
           <div className="proj-txtx">
             <h4>{title}</h4>
             <span>{description}</span>
             <br></br>
             <a href={linkGit} target="_blank"><img class="card-icon" src={navIcon2} alt=""/></a>
             <a href={linkLive} target="_blank"><img class="card-icon" src={navIcon4} alt=""/></a>
           </div>
         </div>
       </Col>
       )
  } else if (typeof(navIcon2) !== 'undefined' && navIcon2 != null) {
   return (
      <Col size={12} sm={6} md={4}>
        <div className="proj-imgbx">
          <img className="proj-img" src={imgUrl} />
          <div className="proj-txtx">
            <h4>{title}</h4>
            <span>{description}</span>
            <br></br>
            <a href={linkGit} target="_blank"><img class="card-icon" src={navIcon2} alt=""/></a>
          </div>
        </div>
      </Col>
      )
    } else if (typeof(navIcon4) !== 'undefined' && navIcon4 != null) {
      return (
         <Col size={12} sm={6} md={4}>
           <div className="proj-imgbx">
             <img className="proj-img" src={imgUrl} />
             <div className="proj-txtx">
               <h4>{title}</h4>
               <span>{description}</span>
               <br></br>
               <a href={linkLive} target="_blank"><img class="card-icon" src={navIcon4} alt=""/></a>
             </div>
           </div>
         </Col>
         )
  } else {
      return (
        <Col size={12} sm={6} md={4}>
          <div className="proj-imgbx">
            <img className="proj-img" src={imgUrl} />
            <div className="proj-txtx">
              <h4>{title}</h4>
              <span>{description}</span>
              <br></br>
            </div>
          </div>
        </Col>
      )  
    }

}
