import { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import 'animate.css';
import TrackVisibility from 'react-on-screen';
import { useForm, ValidationError } from '@formspree/react';

export const Contact = () => {

  const [state, handleSubmit] = useForm("xgebgjoo");
  if (state.succeeded) {
    return <p>Thanks for contacting me!</p>;
  }

  return (
    <section className="contact" id="connect">
      <Container>
        <Row className="align-items-start">
          <Col size={12} md={6}>
            <h2 className="contact-header">Contacts</h2>
            <div className="contact-box">
              <ul className="contact-list-info">
                <li className="contact-quote"> <p className="text-quote">Any fool can write code that a computer can understand. Good programmers write code that humans can understand. </p> - Martin Fowler -</li>
                <li className="contact-info"><h5>Email: gianlucarea.work@gmail.com</h5></li>
                <li className="contact-info"><h5>Location: L'Aquila, Italy</h5></li>
              </ul>
            </div>
          </Col>
          <Col size={12} md={6}>
            <TrackVisibility>
              {({ }) =>
                <div >
                  <h2>Get In Touch</h2>
                  <form onSubmit={handleSubmit}>
                    <Row>
                      <Col size={12}  className="px-1">
                        <input type="email" placeholder="Email Address" id="email" name="email" />
                        <ValidationError
                          prefix="Email"
                          field="email"
                          errors={state.errors}
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col size={12} className="px-1">
                        <textarea rows="7" placeholder=" Your Message" id="message" name="message"></textarea>
                        <ValidationError
                          prefix="Message"
                          field="message"
                          errors={state.errors}
                        />
                        <button type="submit" ><span>Send</span></button>
                      </Col>
                    </Row>
                  </form>
                </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
    </section>
  )
}
